import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router';
import { connect } from 'react-redux';
import { changeTrack } from '../actions';
import { motion } from 'framer-motion';
import Topnav from '../component/topnav/topnav';
import TextRegularM from "../component/text/text-regular-m";
import PlayButton from '../component/buttons/play-button';
import ArtistDetails from '../component/playlist/artist/artist-details';
import ArtistFooter from '../component/playlist/artist/artist-footer';
import PlaylistTrack from '../component/playlist/artist/artist-track';
import TitleL from '../component/text/title-l';
import { calcularDuracionPlaylist } from '../utils/utils';
import styles from './artist-page.module.css';
import { PLAYLIST } from '../data/index';
import ArtistStream from "../component/playlist/artist/artistStream";
import Recomendacion from "../component/playlist/artist/recomendacion";

function PlaylistPage(props) {
    const [playlistIndex, setPlaylistIndex] = useState(undefined);
    const [isThisPlay, setIsThisPlay] = useState(false);
    const { path } = useParams();
    const [showArtistStream, setShowArtistStream] = useState(false); // Estado para controlar visibilidad

    useEffect(() => {
        setIsThisPlay(playlistIndex === props.trackData.trackKey[0]);
    }, [playlistIndex, props.trackData.trackKey]);

    useEffect(() => {
        const fetchPlaylist = () => {
            const playlist = PLAYLIST.find(
                item => item.link === path && item.type === "djs"
            );
            if (playlist) {
                changeBg(playlist.playlistBg);
                setPlaylistIndex(PLAYLIST.indexOf(playlist));
                setShowArtistStream(true); // Mostrar ArtistStream si el tipo es "djs"
            } else {
                setShowArtistStream(false); // Ocultar ArtistStream si no hay playlist de tipo "djs"
            }
        };

        fetchPlaylist();
    }, [path]);

    function changeBg(color) {
        document.documentElement.style.setProperty('--hover-home-bg', color);
    }

    const handleSongClick = async (item, song) => {
        try {
            const playlistIndex = PLAYLIST.indexOf(item);
            const songIndex = item.playlistData.findIndex(s => s.index === song.index);

            if (playlistIndex !== -1 && songIndex !== -1) {
                props.changeTrack([playlistIndex, songIndex]);
                const songAudio = await props.loadAudio(song.songUrl);
                props.playAudio(songAudio);
            } else {
                console.error('Error: Pista no encontrada');
            }
        } catch (error) {
            console.error('Error durante la reproducci贸n de la canci贸n:', error);
        }
    };

    return (
        <motion.div
            className={styles.PlaylistPage}
            initial={{ opacity: 0, scale: 1.0001 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: .6, ease: "easeInOut" }}
        >
            <div className={styles.gradientBg}></div>
            <div className={styles.gradientBgSoft}></div>
            <div className={styles.Bg}></div>

            <Topnav />

            {PLAYLIST.map((item) => {
                if (item.link === path) {
                    const duracionTotal = calcularDuracionPlaylist(item.playlistData);
                    const totalCanciones = item.playlistData.length;

                    // Ahora pasamos el artista actual a Recomendacion
                    const artistData = item; // 'item' es el DJ actual con su informaci贸n

                    return (
                        <div key={item.title}>
                            {/* Mostrar ArtistStream solo si est谩 disponible */}
                            {showArtistStream && <ArtistStream className={styles.fixedDj} dj={item} />}

                            <div className={styles.perfilGrid}>
                                <ArtistDetails 
                                    data={item} 
                                    duracionTotal={duracionTotal} 
                                    totalCanciones={totalCanciones}
                                />
    
                                <div>
                                    <Recomendacion data={artistData} />
                                </div>
                            </div>
                        
                            <div className={styles.PlaylistIcons}>
                                <div onClick={() => handleSongClick(item, item.playlistData[0])}>
                                    <PlayButton isthisplay={isThisPlay} />
                                </div>
                            </div>

                            <div className={styles.ListHead}>
                                <TextRegularM>#</TextRegularM>
                                <TextRegularM>T铆tulo</TextRegularM>
                                <TextRegularM>Duraci贸n</TextRegularM>
                            </div>

                            <div className={styles.PlaylistSongs}>
                                {item.playlistData.map((song) => (
                                    <div 
                                        key={song.index} 
                                        onClick={() => handleSongClick(item, song)} 
                                        className={styles.SongBtn}
                                    >
                                        <PlaylistTrack 
                                            data={{
                                                listType: item.type,
                                                song: song
                                            }}
                                        />
                                    </div>
                                ))}
                            </div>

                            <ArtistFooter data={item} />
                        </div>
                    );
                }
            })}
        </motion.div>
    );
}

const mapStateToProps = (state) => ({
    trackData: state.trackData,
});

export default connect(mapStateToProps, { changeTrack })(PlaylistPage);
