import React, { useState } from "react";
import TitleS from "../../text/title-s";
import TextBoldM from "../../text/text-bold-m";
import TextRegularM from "../../text/text-regular-m";
import styles from "./artist-details.module.css";
import FormularioPedido from "./modalStream/pedidoMusical";

function ArtistDetails({ data, totalCanciones, duracionTotal, showDjEnDirecto }) {
    const [showForm, setShowForm] = useState(false);

    const handleOpenForm = () => setShowForm(true);
    const handleCloseForm = () => setShowForm(false);

    return (
        <div className={`${styles.playlistDetails} ${showDjEnDirecto ? styles.marginTop : ''}`}>
            <div className={styles.imgBox}>
                <img src={data.imgUrl} alt={`${data.title} cover`} />
            </div>
            <div className={styles.textBox}>
                <h1>{data.title}</h1>
                <div className={styles.dataArtist}>
                    <div className={styles.Artist}>
                        <figure className={styles.artistshadow}>
                            <img src={data.artistImgUrl} alt={`${data.artist} image`} />
                        </figure>
                    </div>
                    <div className={styles.Artist}>
                        <TextRegularM>{totalCanciones} Sets</TextRegularM>
                    </div>
                    <div className={styles.ArtistShow}>
                        <TextRegularM>{duracionTotal}</TextRegularM>
                    </div>
                </div>
            </div>

            {showForm && (
                <FormularioPedido
                    artist={data.artist}
                    discoteca={data.club}
                    djImage={data.artistImgUrl}
                    onClose={handleCloseForm}
                />
            )}
        </div>
    );
}

export default ArtistDetails;