import React from "react";
import PropTypes from "prop-types";
import styles from "./pedido-musical.module.css";

const FormularioPedido = ({ artist, telefono, club, djImage, onClose }) => {
    const handleSubmit = (e) => {
        e.preventDefault();
        
        // Obt茅n los valores de los campos del formulario
        const songName = e.target.elements.songName.value;
        const songArtist = e.target.elements.songArtist.value;
        const comment = e.target.elements.songComment.value;

        // Construye el mensaje
        const message = `Hey DJ\n\nCanci贸n: ${songName}\nArtista: ${songArtist}\nComentario: ${comment}`;

        // Construye la URL de WhatsApp con el n煤mero de tel茅fono proporcionado
        const encodedMessage = encodeURIComponent(message);
        const whatsappUrl = `https://wa.me/${telefono}?text=${encodedMessage}`;

        // Abre WhatsApp con el mensaje preconfigurado
        window.open(whatsappUrl, "_blank");

        onClose(); // Cerrar modal despu茅s de enviar
    };

    return (
        <div className={styles.modalBackground}>
            <div className={styles.modalContent}>
                <div
                    className={styles.imageSection}
                    style={{ backgroundImage: `url(${djImage})` }}
                >
                    <h1 className={styles.artistName}>{artist}</h1>
                </div>
                <form onSubmit={handleSubmit} className={styles.form}>
                    <label>
                        Nombre de la canci贸n:
                        <input type="text" name="songName" required />
                    </label>
                    <label>
                        Artista:
                        <input type="text" name="songArtist" required />
                    </label>
                    <label>
                        Comentario:
                        <input type="text" name="songComment" />
                    </label>

                    {/* <div className={styles.donationOptions}>
                        <label>
                            <input type="radio" name="donation" value="2" required /> <br />
                            2
                        </label>
                        <label>
                            <input type="radio" name="donation" value="4" required /> <br />
                            4
                        </label>
                        <label>
                            <input type="radio" name="donation" value="6" required /> <br />
                            6
                        </label>
                    </div>
                    <h1 className={styles.cobro}>*No es obligatorio</h1> */}

                    <button type="submit" className={styles.submitButton}>
                        A帽adir a la cola
                    </button>
                </form>
            </div>
        </div>
    );
};

FormularioPedido.propTypes = {
    artist: PropTypes.string.isRequired,
    telefono: PropTypes.string.isRequired,
    club: PropTypes.string.isRequired,
    djImage: PropTypes.string.isRequired,
    onClose: PropTypes.func.isRequired,
};

export default FormularioPedido;
