import React from "react";
import PropTypes from "prop-types";
import styles from "./ubicacion.module.css";

// Funci贸n para formatear la fecha en el formato deseado
const formatDate = (dateString) => {
    const date = new Date(dateString);
    const options = { day: 'numeric', month: 'short', hour: '2-digit', minute: '2-digit', hour12: false };
    return date.toLocaleDateString('es-ES', options).replace('.', '');
};

const Ubicacion = ({ club, clubImg, clubmapa, clubtime, endtime }) => {
    const handleOpenMaps = () => {
        const encodedAddress = encodeURIComponent(clubmapa); // Convierte la direcci贸n en un formato seguro para URL
        const mapsUrl = `https://www.google.com/maps/search/?api=1&query=${encodedAddress}`;
        window.open(mapsUrl, "_blank"); // Abre la direcci贸n en una nueva pesta帽a
    };

    return (
        <div className={styles.modalBackground}>
            <div className={styles.modalContent}>
                <div
                    className={styles.imageSection}
                    style={{ backgroundImage: `url(${clubImg})` }}
                >
                    <h1 className={styles.discotecaName}>{club}</h1>
                    <p className={styles.discotecaName}>{formatDate(clubtime)} - {formatDate(endtime)}</p>
                </div>

                <div className={styles.mapSection}>
                    <p>
                     {clubmapa}
                    </p>
                </div>

                <button
                    type="button"
                    className={styles.submitButton}
                    onClick={handleOpenMaps}
                >
                    Abrir en Google Maps
                </button>
            </div>
        </div>
    );
};

Ubicacion.propTypes = {
    club: PropTypes.string.isRequired,
    djImage: PropTypes.string.isRequired,
    clubtime: PropTypes.string.isRequired,
    endtime: PropTypes.string.isRequired,
    djImage: PropTypes.string.isRequired,
    clubmapa: PropTypes.string.isRequired,
    onClose: PropTypes.func.isRequired,
};

export default Ubicacion;
