import React from 'react';
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper/modules";
import "swiper/css"; // Importa estilos b谩sicos de Swiper
import TitleS from '../../text/title-s';
import TextBoldM from '../../text/text-bold-m';
import styles from "./artist-footer.module.css";

function ArtistFooter({ data }) {
  // Verificar si hay im谩genes en la playlist
  const hasImages = data.playlistImages && data.playlistImages.length > 0;

  // Renderizar el Swiper si hay im谩genes
  const renderSwiper = () => {
    if (!hasImages) return null;

    return (
      <Swiper
        modules={[Autoplay]}
        autoplay={{ delay: 2500, disableOnInteraction: false }}
        loop={true}
        speed={1800}
        spaceBetween={10} // Espacio entre slides
        slidesPerView={1} // Muestra una imagen por vez
        style={{ aspectRatio: '4 / 4' }} // Mantiene proporci贸n 5x8
        className={styles.swiperContainer}
      >
        {data.playlistImages.map((image, index) => (
          <SwiperSlide key={index}>
            <img src={image} alt={`Slide ${index + 1}`} className={styles.image} />
          </SwiperSlide>
        ))}
      </Swiper>
    );
  };

  return (
    <div className={styles.footerDetails}>
      {renderSwiper()}

      <div className={styles.textBox}>
        <div className={styles.Artist}>
          <figure>
            <img src={data.artistImgUrl} alt="Artist" />
          </figure>
          <div className={styles.boxBio}>
            <a className={styles.textBio} href={data.redSocial} target="_blank" rel="noopener noreferrer">Seguir</a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ArtistFooter;