import React, { useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper/modules";
import "swiper/css";
import styles from "./Recomendacion.module.css";
import * as Icons from "../../icons"; // Aseg煤rate de tener los 铆conos Play/Pause

const Recomendacion = ({ data }) => {
  const [isPlaying, setIsPlaying] = useState(false); // Estado para manejar si est谩 reproduciendo
  const [currentTrack, setCurrentTrack] = useState(null); // Estado para la pista actual

  if (!data.recomendacion || !data.artistBio || !data.playlistData) {
    return null; // Si no existen las propiedades, no mostrar el componente.
  }

  const pistaRecomendada = data.pistaRecomendada || 0; // Si no hay valor, usa el 铆ndice 0
  const selectedTrack = data.playlistData[pistaRecomendada]; // Usamos pistaRecomendada para seleccionar la pista

  const handlePlayPause = () => {
    if (isPlaying && currentTrack === selectedTrack.songName) {
      // Pausar si est谩 reproduciendo esta pista
      setIsPlaying(false);
      setCurrentTrack(null);
      // L贸gica para pausar el audio aqu铆
    } else {
      // Reproducir esta pista
      setIsPlaying(true);
      setCurrentTrack(selectedTrack.songName);
      // L贸gica para reproducir el audio aqu铆
    }
  };

  return (
    <div className={styles.Recomendacion}>
      {/* Caja superior con artistBio */}
      {data.artistBio && (
        <div className={styles.BioContainer}>
          <img src={data.artistImgUrl} alt={`${data.artist} image`} />
          <p className={styles.ArtistBio}>{data.artistBio}</p>
        </div>
      )}

      {/* Fondo con Swiper */}
      {data.recomendacion && (
        <Swiper
          className={styles.SwiperContainer}
          spaceBetween={0}
          slidesPerView={1}
          loop={true}
          speed={2500}
          autoplay={{
            delay: 3000,
            disableOnInteraction: false,
          }}
          modules={[Autoplay]}
        >
          {data.recomendacion.map((img, index) => (
            <SwiperSlide className={styles.SwiperSlide} key={index}>
              <img src={img} alt={`Recomendaci贸n ${index + 1}`} />
            </SwiperSlide>
          ))}
        </Swiper>
      )}

      {selectedTrack && (
        <div className={styles.TrackContainer}>
          <img
            src={selectedTrack.songimg}
            alt={selectedTrack.songName}
            className={styles.TrackImage}
          />
          <div className={styles.TrackInfo}>
            <p className={styles.SongName}>{selectedTrack.songName}</p>
            <p className={styles.SongArtist}>{selectedTrack.songArtist}</p>
          </div>
          {/* <button className={styles.PlayPauseButton} onClick={handlePlayPause}>
            {isPlaying && currentTrack === selectedTrack.songName ? (
              <Icons.Pause />
            ) : (
              <Icons.Play />
            )}
          </button> */}
        </div>
      )}
    </div>
  );
};

export default Recomendacion;