import React, { useState, useEffect } from "react";
import Ubicacion from "./modalStream/ubicacion";
import FormularioPedido from "./modalStream/pedidoMusical";
import styles from "./artistStream.module.css";

function DjEnDirecto({ dj }) {
    const [openModal, setOpenModal] = useState(null); // Controla cu谩l modal est谩 abierto
    const [isLive, setIsLive] = useState(false);
    const [isHidden, setIsHidden] = useState(false); // Controla si el componente est谩 escondido

    const handleToggleModal = (modalId) => {
        setOpenModal((prev) => (prev === modalId ? null : modalId)); // Alterna entre abrir/cerrar
    };

    const toggleVisibility = () => {
        setIsHidden((prev) => !prev); // Alterna la visibilidad
        setOpenModal(null); // Cierra cualquier modal abierto
    };

    const handleCloseModal = () => {
        setOpenModal(null); // Cierra cualquier modal abierto
    };

    useEffect(() => {
        if (!dj.clubtime || !dj.endtime) {
            setIsLive(false);
            return;
        }

        const now = new Date();
        const startTime = new Date(dj.clubtime);
        const endTime = new Date(dj.endtime);

        // Log para debugging
        console.log("Now: ", now);
        console.log("Start Time: ", startTime);
        console.log("End Time: ", endTime);

        setIsLive(now >= startTime && now <= endTime);
    }, [dj]);

    if (!dj.club || !isLive) return null;

    return (
        <div
            className={`${styles.djEnDirecto} ${isHidden ? styles.hidden : ""}`}
        >
            <div className={styles.details}>
                <p className={styles.detailsTitle}>
                    <strong>{dj.title}</strong> est谩 en directo{""}
                </p>
                <div className={styles.gridDirecto}>
                    <div className={styles.grid}>
                        <button
                            className={styles.requestButton}
                            onClick={() => handleToggleModal("anotherModal1")}
                            disabled={isHidden} // Deshabilita el bot贸n si est谩 oculto
                        >
                            <strong>Ubicaci贸n</strong>
                        </button>
                    </div>
                    <div className={styles.grid}>
                        <button
                            className={styles.requestButton}
                            onClick={() => handleToggleModal("songRequest")}
                            disabled={isHidden} // Deshabilita el bot贸n si est谩 oculto
                        >
                            Pedir una <br /> Canci贸n
                        </button>
                    </div>
                </div>
            </div>

            <div
                onClick={toggleVisibility}
                className={`${styles.iconClose} ${isHidden ? styles.restore : ""}`}
            >
                {isHidden ? " " : " "}
            </div>

            {/* Modal de solicitud de canci贸n */}
            <div
                className={`${styles.modal} ${
                    openModal === "songRequest" ? styles.open : styles.closed
                }`}
            >
                {openModal === "songRequest" && (
                    <FormularioPedido
                        artist={dj.title}
                        telefono={dj.telefono}
                        club={dj.club}
                        djImage={dj.artistImgUrl}
                        onClose={handleCloseModal} // Pasar onClose aqu铆
                    />
                )}
            </div>

            {/* Modal de ubicaci贸n */}
            <div
                className={`${styles.modal} ${
                    openModal === "anotherModal1" ? styles.open : styles.closed
                }`}
            >
                {openModal === "anotherModal1" && (
                    <Ubicacion
                        artist={dj.title}
                        club={dj.club}
                        clubImg={dj.clubImg}
                        djImage={dj.artistImgUrl}
                        clubtime={dj.clubtime}
                        endtime={dj.endtime}
                        clubmapa={dj.clubmapa}
                        onClose={handleCloseModal} // Pasar onClose aqu铆
                    />
                )}
            </div>
        </div>
    );
}

export default DjEnDirecto;
